.app{
	display: flex;
	padding: 30px;
	justify-content: space-around;
}
.game{
	width: 560px;
	height: 560px;
	display: flex;
	flex-wrap: wrap;
}
.game img {
	width: 70px;
	height: 70px;
}
.score {
	padding: 5px;
	border-radius: 1px;
	border: 2px solid black;
	font-size: larger;
	height: 100px;
	width: 100px;
	align-self: center;
	text-align: center;
}